.page-wrapper {
    min-width: 320px;
    max-width: 1200px;
    margin: 0 auto;
    padding-left: $padding-mobile;
    padding-right: $padding-mobile;
    overflow: hidden;

    @media (min-width: $tablet-width) {
        padding-left: $padding-tablet;
        padding-right: $padding-tablet;
    }
}
