.second-nav__items {

    position: absolute;
    top: 90px;
    left: 0;
    display: none;
    flex-wrap: wrap;
    width: 100%;

    outline: 1px solid red;


    z-index: 2;
    opacity: 1;
    background-color: white;
}

.second-nav__item {
    width: 50%;
    padding: 10px 10px 10px 40px;
    font-size: 18px;
    line-height: 24px;

    &:hover {
        background-color: rgba(102, 102, 102, 0.1);
    }
}

.second-nav__item a {
    color: #666;
    text-decoration: none;
}
