.help {
    position: relative;
    left: 50%;
    width: 100vw;
    overflow: hidden;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    padding: 20px 20px 20px 30px;
    box-sizing: border-box;

    color: white;

    background-color: #0087bf;
    transform: translate(-50%);

    @media (min-width: $tablet-width) {
        position: static;

        flex-direction: row;
        width: 100%;
        padding: 16px 26px 16px 30px;

        transform: none;
    }
}

.help--insert {
    margin-bottom: 45px;
    overflow: hidden;
}

.help__offer {
    padding: 0;
    margin: 0;
    margin: 0 0 14px;

    font-size: 24px;
    line-height: 30px;

    @media (min-width: $tablet-width) {
        margin: 0;
        width: 62%;
    }
}

.help__link {
    position: relative;
    padding: 0 40px 0 0;
    margin: 0;

    &::after {
        position: absolute;
        content: "";
        top: 4px;
        right: 7px;

        width: 23px;
        height: 23px;

        background-image: url("../../img/icon-TFFollow.svg");
        background-repeat: no-repeat;
        background-size: contain;
    }
}

.help__link a {
    font-size: 20px;
    line-height: 30px;
    color: white;

    &:hover {
        text-decoration: none;
        border-bottom: 1px solid white;
    }
}
