.socials {
    margin-bottom: 20px;

    @media (min-width: $tablet-width) {
        width: 31.3%;
        margin-bottom: 0;
    }
}

.socials__list {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 140px;
}

.socials__icon {
    border-radius: 3px;
    margin-bottom: 6px;
    color: grey;
}

.socials__icon--vk:hover {
    color: #4680c2;
}

.socials__icon--fb:hover {
    color: #4267b2;
}

.socials__icon--app:hover {
    color: black;
}

.socials__icon--ok:hover {
    color: #ee8208;
}

.socials__icon--ytb:hover {
    color: red;
}

.socials__icon--inst:hover {
    color: #d93175;
}



