.news {
    display: flex;
    flex-direction: column;
    padding-top: 40px;
}

.news__pic {
    margin-bottom: 108px;
}

.news__heading {
    font-size: 24px;
    line-height: 24px;
    padding-top: 25px;
    padding-left: 20px;
    padding-right: 20px;
    margin: 0 0 10px;
    font-weight: normal;
}

a .news__item-heading {
    color: black;
}

a .news__desc {
    color: #666666;
}

a .news__time {
    color: black;
}

.news__item a:hover {
    text-decoration: none;
}

.news__wrapper {
    box-sizing: border-box;
    width: 84%;
    margin-left: auto;

    border-top: 2px solid #ff7033;
    box-shadow: 0 5px 5px rgba(0,0,0,.11);
}

.news__item {
    padding-left: 15px;
    padding-right: 20px;
    padding-top: 10px;
    padding-bottom: 10px;

    &:not(:last-child) {
        border-bottom: 1px dotted grey;
    }

    &:hover {
        background-color: rgba(102, 102, 102, 0.1);
        background-clip: padding-box;
    }
}

.news__time {
    font-size: 14px;
    font-weight: bold;
}

.news__item-heading {
    margin: 0;
    font-size: 15px;
    line-height: 20px;
    font-weight: normal;
}

.news__desc {
    font-size: 15px;
    line-height: 20px;
}
