body {
    padding: 0;
    margin: 0;

    font-family: "Tensorfont", "Helvetica", sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 22px;
    color: #666666;

    overflow-x: hidden;
    word-wrap: break-word;

    @media (min-width: $tablet-width) {
        font-size: 15px;
        line-height: 22px;
    }

    @media (min-width: $desktop-width) {
        font-size: 16px;
        line-height: 23px;
    }
}

img {
    width: 100%;
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;
    color: #0055bb;

    &:hover {
        text-decoration: underline;
    }
}


