.information {
    margin-bottom: 27px;

    @media (min-width: $tablet-width) {
        display: flex;
        justify-content: flex-start;
        flex-wrap: wrap;

        padding-top: 35px;
        margin-bottom: 0px;

        border-bottom: 1px solid #e4e4e4;
    }
}

.information__heading {
    position: relative;
    left: 50%;
    transform: translate(-19.7%);
    box-sizing: border-box;
    width: 98vw;
    padding: 17px 30px 10px 50px;
    margin-top: -1px;

    border-top: 1px solid #e4e4e4;
    border-bottom: 1px solid #e4e4e4;

    overflow: hidden;
    cursor: pointer;

    &::after {
        position: absolute;
        content: "";
        top: 16px;
        left: 26px;

        width: 20px;
        height: 20px;

        background-image: url("../../img/icon-Expand.svg");
        opacity: 0.5;
        transform: rotate(270deg);
        background-size: contain;
    }

    @media (min-width: $tablet-width) {
        position: static;
        transform: none;
        padding: 0;
        margin-bottom: 10px;
        width: auto;

        font-size: 18px;

        border: none;

        &::after {
            display: none;
        }
    }
}

.information__thing {
    width: 33.3%;


    @media (min-width: $tablet-width) {
        margin-bottom: 40px;
    }

    @media (min-width: $tablet-width) {
        width: 25%;
    }
}

.information__item {
    display: none;

    @media (min-width: $tablet-width) {
        display: flex;

        font-size: 13px;
        line-height: 20px;
    }
}

.information__item a {
    color: inherit;
}

.information__item--line {
    position: relative;
    padding-bottom: 15px;

    &:after {
        position: absolute;
        content: "";
        left: 0;
        top: 24px;
        width: 181px;
        height: 2px;
        border-bottom: 1px dotted silver;
    }
}
