.submenu__container {
    display: none;
}

.submenu__container--opened {
    position: absolute;
    top: 100px;
    left: 0;
    display: flex;
    flex-wrap: wrap;
    padding: 10px 25px 15px;
    width: 100%;
    overflow: hidden;

    background-color: white;
    z-index: 2;


    @media (min-width: $tablet-width) {
        top: 145px;
    }

    @media (min-width: $desktop-width) {
        top: 95px;
    }
}

.submenu__thing {
    width: 93%;
    margin-bottom: 15px;
    // outline: 1px solid blue;

    @media (min-width: $tablet-width) {
        width: 45%;
        margin-right: 5%;
    }

    @media (min-width: $desktop-width) {
        width: 30%;
        margin-right: 3%;
    }
}

.submenu__pic {
    width: 65px;
    margin-right: 15px;
}

.submenu__desc {
    display: flex;
    margin: auto 0;
    width: 120px;

    font-size: 15px;
    line-height: 22px;
}

.submenu__thing a {
    display: flex;
    width: 100%;

    color: #666666;
    // box-shadow: 0 -14px 20px 0 #000;

    // outline: 1px solid red;

    &:hover,
    &:focus {
        text-decoration: none;
        border-radius: 10px;
        background-color: #f9f9f9;
        box-shadow: 0 2px 11px 0 #6666;
        outline: none;
    }
}
