.second-heading {
    margin: 0 0 15px 0;

    font-size: 20px;
    font-weight: normal;
    line-height: 24px;
    color: black;

    @media (min-width: $tablet-width) {
        font-size: 20px;
        line-height: 24px;
    }

    @media (min-width: $desktop-width) {
        font-size: 25px;
        line-height: 31px;
    }
}

.second-heading--center {
    text-align: center;
}
