.breadcrumbs {
    display: none;



    @media (min-width: $tablet-width) {
        display: block;
    }
}

.breadcrumbs__item a {
    color: $breadcrumbs-color;
}

.breadcrumbs__list {
    display: flex;
    font-size: 14px;
}

.breadcrumbs__item:not(:first-child) {
    position: relative;
    padding-left: 15px;

    &:before {
        position: absolute;
        content: "";
        top: 2px;
        left: 0;

        width: 15px;
        height: 15px;

        background-image: url("../../img/triangle.svg");
        background-size: contain;
    }
}
