.equip__list {
    padding-top: 10px;

    @media (min-width: $tablet-width) {
        padding-top: 15px;
    }
}

.equip__item a {
    display: flex;
    flex-direction: column-reverse;
    align-items: center;
    padding-bottom: 20px;
    margin-bottom: 15px;

    color: inherit;

    border: 1px  solid #e4e4e4;
    border-radius: 10px;

    &:hover {
        border: 1px solid blue;
        background-color: #e4e4e4;
        text-decoration: none;
    }

    @media (min-width: $tablet-width) {
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;

        padding-bottom: 32px;
        margin-bottom: 10px;
    }
}

.equip__desc {
    position: relative;
    width: 298px;
    padding-top: 20px;

    @media (min-width: $tablet-width) {
        padding-top: 20px;
        padding-left: 30px;
        width: 60%;
    }

    @media (min-width: $desktop-width) {
        width: 49%;
    }
}

.equip__heading {
    margin: 0 0 10px;
    width: 180px;

    font-size: 20px;
    line-height: 24px;
    font-weight: normal;
    color: black;

    @media (min-width: $tablet-width) {
        width: 77%;
    }

    @media (min-width: $desktop-width) {
        width: 73%;
    }
}

.equip__for {
    margin: 0;

    font-size: 14px;
    line-height: 18px;
    text-decoration: underline;
}

.equip__get-app {
    margin: 0;
    font-size: 14px;
    line-height: 18px;
}

.equip__features-list {
    @media (min-width: $desktop-width) {
        width: 66%;
    }
}

.equip__features-item {
    font-size: 14px;
    line-height: 18px;
}

.equip__price {
    position: absolute;
    top: 20px;
    left: 214px;

    font-size: 20px;
    line-height: 24px;
    color: black;

    @media (min-width: $tablet-width) {
        font-size: 15px;
        left: 295px;
    }

    @media (min-width: $desktop-width) {
        left: 77%;
    }
}

.equip__price--free {
    font-size: 14px;
    left: 235px;

    &::after {
        content: "";
        position: absolute;
        top: 25px;
        left: 0;

        width: 62px;
        height: 30px;

        opacity: 0.5;

        background-image: url("../../img/icon-gp.svg");
        background-repeat: no-repeat;
        background-size: contain;
    }

    @media (min-width: $tablet-width) {
        left: 295px;

        &::after {
            left: 83px;
            top: 0;
        }
    }

    @media (min-width: $desktop-width) {
        left: 77%;
    }
}

.equip__pseudo-link {
    color: blue;

    &:hover {
    text-decoration: underline  ;
    }
}
