.fine {
    margin-bottom: 30px;

    @media (min-width: $tablet-width) {
        margin-bottom: 20px;
    }
}

.fine__list {
    display: flex;
    flex-direction: column;

    padding-top: 10px;

    @media (min-width: $tablet-width) {
        flex-direction: row;
        justify-content: space-between;
    }
}

.fine__item {
    width: auto;
    padding: 15px 10px 15px 13px;

    text-align: center;
    background-color: #f3f7fc;

    border-radius: 5px;

    &:not(:last-child) {
        margin-bottom: 15px;
    }

    @media (min-width: $tablet-width) {
        width: 26%;
        padding: 16px;

        text-align: left;

        &:not(:last-child) {
            margin-bottom: 0;
        }
    }
}
