.page-header {
    display: flex;
    padding-top: 8px;
    padding-bottom: 5px;

    background-color: #fff;
    box-shadow: 0 -14px 20px 0 black;

    @media (min-width: $tablet-width) {
        padding-top: 20px;
        box-shadow: none;
    }

    @media (min-width: $desktop-width) {
        padding-bottom: 28px;
    }
}

.page-header__logo {
    display: block;
    margin: 0;
    padding: 0;

    &:hover,
    &:focus {
        outline: none;
        opacity: 0.5;
    }

    &:active {
        opacity: 0.1;
    }
}

.page-header__logo-img {
    display: block;
    margin: 0;
    padding: 0;
}



