.phones {
    margin-bottom: 40px;

    @media (min-width: $tablet-width) {
        width: 31.3%;
        margin-bottom: 0;
    }
}

.phones a {
    @media (min-width: $tablet-width) {
        color: inherit;
    }
}

.phones__item {
    font-size: 18px;

    @media (min-width: $tablet-width) {
        font-size: 15px;

    }
}

.phones__item:last-child {
    display: none;

    @media (min-width: $tablet-width) {
        display: block;
    }
}
