.intro {
    position: relative;
    margin-bottom: 25px;
}

.intro__time {
    position: absolute;
    top: -37px;
    right: 2px;

    padding-left: 20px;

    font-size: 14px;
    color: #999999;

    &::after {
        position: absolute;
        content: "";
        left: 0px;
        width: 35px;
        height: 17px;

        background-image: url("../../img/icon-Calendar2.svg");
        background-size: contain;
        background-repeat: no-repeat;
    }
}
