.deadline {
    margin-bottom: 25px;

    @media (min-width: $tablet-width) {
        margin-bottom: 16px;
    }
}

.deadline__item {
    position: relative;

    padding-left: 17px;
    margin-bottom: 5px;

    &::before {
        content: "";
        position: absolute;
        left: 2px;
        top: 11px;

        width: 5px;
        height: 5px;
        border-radius: 50%;
        background-color: #0087bf;
    }
}


