.work-sheme {
    margin-bottom: 5px;

    @media (min-width: $tablet-width) {
        margin-bottom: 15px;
    }
}
.work-sheme__pic {
    margin-bottom: 15px;
}

.work-sheme__columns {
    display: flex;
    justify-content: space-between;
}

.work-sheme__list {
    display: flex;
    flex-flow: column wrap;
    width: 49%;
}

.work-sheme__item {
    margin-bottom: 15px;
}

