.main-nav {
    padding-top: 18px;
    margin-left: auto;

    @media (min-width: $desktop-width) {
        display: flex;
        padding-top: 20px;
    }
}

.main-nav__list {
    display: none;

    @media (min-width: $desktop-width) {
        display: flex;
        flex-wrap: wrap;
    }
}

.main-nav__list--opened {
    position: absolute;
    top: 55px;
    left: 0;
    width: 100%;
    display: flex;
    justify-content: space-between;



    background-color: white;
    z-index: 2;

    @media (min-width: $tablet-width) {
        top: 85px;
        justify-content: flex-end;

        border-top: 1px solid rgba(102, 102, 102, 0.1);
    }

    @media (min-width: $desktop-width) {
        position: static;
        border-top: none;
    }
}

.main-nav__item {
    padding-right: 32px;
    font-size: 20px;
    line-height: 24px;
}

.main-nav__item a {
    color: #666666;

    &:hover,
    &:focus {
        outline: none;
        text-decoration: none;

        @media (min-width: $tablet-width) {
            &::after {
                position: absolute;
                content: "";
                top: 35px;
                left: 0;
                width: 100%;
                height: 4px;
                background-color: #666666;
                opacity: 0.5;
            }
        }
    }

    @media (min-width: $tablet-width) {
        position: relative;


        &:before {
            position: absolute;
            content: "";
            top: 10px;
            right: -20px;
            width: 3px;
            height: 11px;
            border-left: 1px dotted grey;
        }
    }
}


.main-nav__item--opened {
    @media (max-width: 699px) {
        width: 33.3%;
        padding: 10px;

        background-color: #f9f9f9;

        text-align: center;
        border: 1px solid rgba(102, 102, 102, 0.1);

        &:hover,
        &:focus {
            background: radial-gradient(ellipse farthest-side at 120px -50px, #d9d9d9, #f5f5f5),
                        radial-gradient(ellipse farthest-side at 120px 50px, #d9d9d9, #f5f5f5);
        }
    }

    @media (min-width: $tablet-width) {
        padding-top: 15px;
        border: none;
        padding-bottom: 20px;

        &:hover,
        &:focus {
            background: none;
        }
    }

    @media (min-width: $desktop-width) {
        padding-top: 0;
        padding-bottom: 0;
    }
}

.main-nav__item--opened a {
    display: block;
    width: 100%;

    @media (min-width: $tablet-width) {
        border: none;
    }
}

.main-nav__toggle {
    position: relative;

    width: 20px;
    height: 20px;

    border: none;
    background-color: transparent;
    outline: none;

    &:hover {
        cursor: pointer;
    }

    @media (min-width: $tablet-width) {
        width: 20px;
        height: 12px;
    }

    @media (min-width: $desktop-width) {
        width: 14px;
        height: 20px;
    }

    &::before {
        content: "";

        position: absolute;
        top: 2px;
        left: 2px;

        width: 18px;
        height: 2px;

        background-color: black;
        box-shadow: 0 8px 0 0 black, 0 16px 0 0 black;
    }

    &:hover::before,
    &:focus::before {
        outline: none;
        background-color: #ff7033;
        box-shadow: 0 8px 0 0 #ff7033, 0 16px 0 0 #ff7033;
    }
}

.main-nav__toggle--opened {
    &::before {
        content: "";
        position: absolute;
        top: 10px;
        left: 2px;

        width: 18px;
        height: 2px;
        transform: rotate(45deg);

        background-color: black;
        box-shadow: none;
    }

        &::after {
        content: "";
        position: absolute;
        top: 10px;
        left: 2px;

        width: 18px;
        height: 2px;
        transform: rotate(-45deg);

        background-color: black;
    }

        &:hover::before,
        &:hover::after,
        &:focus::before,
        &:focus::after {
        outline: none;
        background-color: #ff7033;
        box-shadow: none;
    }
}

