.page-main {
    padding-top: 35px;
    overflow: hidden;


    @media (min-width: $tablet-width) {
        padding-top: 25px;
    }

    @media (min-width: $desktop-width) {
        display: flex;
        padding-top: 0;
    }
}

.page-main__header {
    margin: 0;
    margin-bottom: 45px;
    padding: 0;

    font-size: 20px;
    color: black;
    line-height: 24px;


    @media (min-width: $tablet-width) {
        font-size: 36px;
        line-height: 46px;
        font-weight: normal;
    }
}
