.should-marked {
    margin-bottom: 25px;

    @media (min-width: $tablet-width) {
        margin-bottom: 10px;
    }
}

.should-marked__table {
    width: 100%;
    margin-bottom: 20px;

    border-collapse: collapse;

    @media (min-width: $tablet-width) {
        margin-bottom: 15px;
    }
}

.should-marked__table th {
    height: 41px;

    font-size: 18px;
    font-weight: normal;
    vertical-align: middle;
    background-color: #eef4fd;

    &:first-child {
        width: 70%;
    }
}

.should-marked__table tr:not(:first-child) {
    border-bottom: 1px solid #eef4fd;
}

.should-marked__field {
    height: 37px;
    padding-left: 18px;

    font-size: 15px;
}

.should-marked__value {
    padding: 0;
    text-align: center;
}

.should-marked__desc {
    margin-bottom: 10px;
}
