.adress {
    margin-bottom: 10px;


    @media (min-width: $tablet-width) {
        width: 31.3%;
        margin-bottom: 0;

        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

.adress__list {
    display: flex;
    flex-direction: column;
}

.adress__item a {
    text-overflow: ellipsis;
}

.adress__item:nth-child(2) {
    display: none;

    @media (min-width: $tablet-width) {
        display: block;
    }
}
